@use './abstracts';

h1 {
  text-align: center;
  font-size: 4rem;
  line-height: 4.5rem;
  margin: 0 auto;
  background: abstracts.$primary-yellow;  
  font-family: 'Amatic SC', cursive;
}

.score {
  margin: 0 auto;
  font-size: 2.5625rem;
  background: rgba(255,255,255,0);
  line-height: 3.25rem;
  border-radius: 1rem;
}

.game {
  width: 80%;
  height: 350px;
  display: flex;
  row-gap: 30px;
  flex-wrap: wrap;
  margin: 20px auto 0;
  cursor: url('../assets/imgs/hammer.png'), default;
}

@media (min-width: 768px) {
  h1 {
    font-size: 6rem;
    line-height: 6.5rem;
  }
  .score {
    font-size: 3.125rem;
  }
}

@media (min-width: 1280px) {
  h1 {
    font-size: 8rem;
    line-height: 8.5rem;
  }
  .score {
    font-size: 4.25rem;
  }
}
